.header-color {
  background: #833ab4;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #fcb045, #fd1d1d, #833ab4);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #fcb045, #fd1d1d, #833ab4); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.landing-grid {
  background: #833ab4;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #fcb045, #fd1d1d, #833ab4);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #fcb045, #fd1d1d, #833ab4); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.resume-right-col {
  background: #27221F;
  color: white;
}

.resume-right-col h2 {
  text-align: left;
  margin-left: 20px;
  font-family: 'Anton';
}

.contact-body {
  margin: auto;
  /*background: grey;*/
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
}

.contact-grid {
  text-align: center;
  justify-content: center;
  width: 80%;
  height: 550px;
  background: white;
  color: black;
  /*border: 2px solid red;*/
  margin-bottom: 20px;
}

.contact-name {
  font-family: 'Anton', sans-serif;
}

.contact-grid p {
  font-family: 'Oxygen', sans-serif;
  font-weight: bold;
}

.contact-grid hr {
  border-top: 5px dotted black;
  width: 50%;
  margin: auto;
}

.contact-list i {
  font-size: 66px;
  padding-right: 5rem;
}
.avatar-img {
  height: 150px;
  padding-top: 5em;
}

.name-text {
  color: white;
  opacity: .8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
  font-family: 'Anton';
}



.banner-text {
  background-color: black;
  opacity: .8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
}

.banner-text h1 {
  font-size: 66px;
  font-weight: bold;
  color: white;
  margin-top: 10px;
}

.banner-text hr {
  border-top: 5px dotted white;
  width: 50px;
  margin: 0px 5px 11px 425px;
  padding: 1.50em;
}

.banner-text p {
  color: white;
  font-size: 20px;
  /*padding: 1em;*/
}

.social-links {
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin-left: 150px;
}
.social-links i {
  color: white;
  font-size: 5em;
  padding: 8px;
}